<script>
import $ from 'jquery';

export default {
  data() {
    return {
      subscriptionInput: '',
    };
  },
  methods: {
    slide() {
      const target = $('.jq_section1').offset().top;
      $('html,body').animate({ scrollTop: target }, 1000);
    },
    subscription() {
      const vm = this;
      vm.$validator.validate().then((valid) => {
        if (!valid) {
          vm.$bus.$emit('message:push', ' Email', 'danger');
        } else {
          vm.$bus.$emit('message:push', '', 'success');
        }
      });
    },
  },
};
